import axios from 'axios';
import React, { useEffect, useState } from 'react';
import {Bar} from 'react-chartjs-2';

const UniDataBarChart =() =>{
    const[ chartData, setChartData]= useState({
		
		datasets: [],
	});
  //  const[ universityCount, setUniversityCount] = useState({});

    const chart =() =>{
        let uniName =[];
        let uniCount =[];
        let uniActiveNames =[];
        let uniActiveCount =[];
        let uniActiveCount2 =[];
        let uniViolatedNames =[];
        let uniViolatedCount =[];
        let uniViolatedCount2 =[];
        let uniInactiveNames =[];
        let uniInactiveCount =[];
        let uniInactiveCount2 =[];
		let uniCancelledNames =[];
		let uniCancelledCount =[];
        let uniCancelledCount2 =[];
		let uniCompletedNames =[];
		let uniCompletedCount =[];
        let uniCompletedCount2 =[];
		
        axios
        .get('https://staff.ucsc.cmb.ac.lk/ugc/getUniversityCount.php')
        .then(response=>{
           // console.log('data')
           // console.log(response.data);
            for(const dataObj of response.data ){
                uniName.push(dataObj.university);
                uniCount.push(dataObj.count);
            }

           // console.log(uniName, uniCount);

            return axios.get('https://staff.ucsc.cmb.ac.lk/ugc/getUniversityActiveCount.php')
        })
        .then(response =>{
          for(const dataObj of response.data ){
            uniActiveNames.push(dataObj.university);
            uniActiveCount.push(dataObj.activeCount);
        }

            for(let i = 0; i < uniActiveNames.length; i++ ){
                let ii = uniName.indexOf(uniActiveNames[i])
                uniActiveCount2.length=uniName.length;
                uniActiveCount2[i]=uniActiveCount[ii];
            }
        
            return axios.get('https://staff.ucsc.cmb.ac.lk/ugc/getUniversityCompletedCount.php')
        })
		.then(response =>{
          for(const dataObj of response.data ){
            uniCompletedNames.push(dataObj.university);
            uniCompletedCount.push(dataObj.completedCount);
        }

            for(let i = 0; i < uniCompletedNames.length; i++ ){
                let ii = uniName.indexOf(uniCompletedNames[i])
                uniCompletedCount2.length=uniName.length;
                uniCompletedCount2[i]=uniCompletedCount[ii];
            }
        
            return axios.get('https://staff.ucsc.cmb.ac.lk/ugc/getUniversityCancelledCount.php')
        })
		.then(response =>{
          for(const dataObj of response.data ){
            uniCancelledNames.push(dataObj.university);
            uniCancelledCount.push(dataObj.cancelledCount);
        }

            for(let i = 0; i < uniCancelledNames.length; i++ ){
                let ii = uniName.indexOf(uniCancelledNames[i])
                uniCancelledCount2.length=uniName.length;
                uniCancelledCount2[i]=uniCancelledCount[ii];
            }
        
            return axios.get('https://staff.ucsc.cmb.ac.lk/ugc/getUniversityViolatedCount.php')
        })
        .then(response =>{    
        for(const dataObj of response.data ){
            uniViolatedNames.push(dataObj.university);
            uniViolatedCount.push(dataObj.violatedCount);
        }

        for(let i = 0; i < uniViolatedNames.length; i++ ){
            let ii = uniName.indexOf(uniViolatedNames[i])
            uniViolatedCount2.length=uniName.length;
            uniViolatedCount2[ii]=uniViolatedCount[i];
            
        }

        return axios.get('https://staff.ucsc.cmb.ac.lk/ugc/getUniversityInactiveCount.php')
    })
    .then (response =>{
        for(const dataObj of response.data ){
            uniInactiveNames.push(dataObj.university);
            uniInactiveCount.push(dataObj.inactiveCount);
        }

        for(let i = 0; i < uniInactiveNames.length; i++ ){
            let ii = uniName.indexOf(uniInactiveNames[i])
            uniInactiveCount2.length=uniName.length;
            uniInactiveCount2[ii]=uniInactiveCount[i];
            
        }
            setChartData({
                labels:uniName,
                datasets: [
                    {
                        label:'Total bonds',
                        data:uniCount,
                        backgroundColor:[
                            'rgba(231, 76, 60, 0.5)'
                        ],
                        borderColor:[
                            'rgba(231, 76, 60, 1)'
                        ],
                        borderWidth:2
                    },
                    {
                        label:'Active bonds',
                        data:uniActiveCount2,
                        backgroundColor:[
                            'rgba(10, 219, 219,0.5)'
                        ],
                        borderColor:[
                            'rgba(10, 219, 219, 1)'
                        ],
                        borderWidth:2
                    },
					{
                        label:'Cancelled bonds',
                        data:uniCancelledCount2,
                        backgroundColor:[
                            'rgba(10, 219, 111,0.5)'
                        ],
                        borderColor:[
                            'rgba(10, 219, 111,1)'
                        ],
                        borderWidth:2
                    },
                    {
                        label:'Violated bonds',
                        data:uniViolatedCount2,
						backgroundColor:[
                            'rgba(249, 231, 159, 0.5)'
                        ],
                        borderColor:[
                            'rgba(249, 231, 159, 1)'
                        ],
                        borderWidth:2
                    },
                    {
                        label:'Completed bonds',
                        data:uniCompletedCount2,
                        backgroundColor:[
                            'rgba(238, 92, 95,0.5)'
                        ],
                        borderColor:[
                            'rgba(238, 92, 95,1)'
                        ],
                        borderWidth:2
                    },
                    {
                        label:'Inactive bonds',
                        data:uniInactiveCount2,
                        backgroundColor:[
                            'rgba(181, 170, 170,0.5)'
                        ],
                        borderColor:[
                            'rgba(181, 170, 170,1)'
                        ],
                        borderWidth:2
                    }
                ]
            })
        })
        
        .catch(function(error){
            console.log(error);
        })
      //  console.log('dataaaaa===.>');
      //  console.log(uniName,uniCount);
    }

    useEffect(() =>{
        chart()
    },[])
    return(
        <div className="charts">
            <div style={{textAlign:'center'}}>
                <h2>Universty Wise Total Bonds</h2>
            </div>
            <div>
                <Bar    data={chartData} 
                        height={400}
                        width={600}
                        options={{
							indexAxis: 'y',
                            plugins: {
                                legend: {
                                    labels: {
                                        // This more specific font property overrides the global property
                                        font: {
                                            size: 14,
                                            family:'monospace'
                                        }
                                    }
                                }
                            },
                            maintainAspectRatio: false, 
                            resposive:true,
                            //title:{text:"jvhjjhj", display:true},
                            scales:{
                                x: {
                                    
									display: true,
                                    ticks:{
                                        beginAtZero: true
                                    }
                                },
                                y: {
                                    display: true  
                                }
                            }
                          
                        }}/>
            </div>
        </div>
    )
}

export default UniDataBarChart;