import React, {useContext} from 'react';
import {BrowserRouter as Router, Switch, Route, Link} from 'react-router-dom';
import {MyContext} from '../contexts/MyContext';
import Insert from './Insert';
import Edit from './Edit';
import View from './View';
import Reports from './Reports'
import {Redirect} from 'react-router';
import UCSClogo from '../Images/UCSClogo.png';

// Importing the Login & Register Componet
import Login from './Login'
import Register from './Register'

function Home(){

    const {rootState,logoutUser} = useContext(MyContext);
    const {isAuth,theUser,showLogin} = rootState;
	let uni = localStorage.getItem('university');
	if(uni == "all") uni = "";
    // If user Logged in
    if(isAuth)
    {
        //return<Redirect to='/view'/>;
        return(
           
            <Router>
      
      
      <nav className="navbar navbar-expand-lg navbar-light" style={{fontFamily:"serif", backgroundColor:"#d9eafc", fontSize:"18 px"}}>
        <div className="container-fluid">
			<img src={UCSClogo} className="d-inline-block align-text-top" alt="logo" style={{  display: 'flex', height:'90px'}} />
			<Link to={'/'} className="navbar-brand"><h3 >UGC Bonds/Agreements System</h3></Link>
				{uni}
			<div className="collapse navbar-collapse" vid="navbar-SupportedContent" style={{  paddingLeft: '10%'}} >
			  <ul className="navbar-nav mr-auto" >
				<li className="nav-item">
				  <Link  to={'/'} className="nav-link" style={{color:"black", fontSize:"19 px"}} ><h5>[All Bond Agreements]</h5></Link>
				</li> 
				<li className="nav-item">
				  <Link to={'/insert'} className="nav-link" style={{color:"black", fontSize:"19 px"}} ><h5>[New Bond Agreement]</h5></Link>
				</li>    
				<li className="nav-item">
				  <Link to={'/reports'} className="nav-link" style={{color:"black", fontSize:"18 px"}}><h5>[Reports]</h5></Link>
				</li> 
			   </ul>				
			   <div className="d-flex">
				<button className="logoutBtn" onClick={logoutUser}>Logout</button> 
			   </div>				
			  
			</div>
		</div>
      </nav>
      <p></p>
	<div className="container-xl">
      <Switch>
		<Route exact path="/" component={View} />
		<Route path='/insert' component={Insert}/>
        <Route path='/edit/:id' component={Edit}/>
        <Route path='/view' component={View}/>
        <Route path='/reports' component={Reports}/>
      </Switch>
      </div>
    </Router>
    
        )
    }
    // Showing Login Or Register Page According to the condition
    else if(showLogin){
        return <Login/>;
    }
    else{
        return <Register/>;
    }
    
}

export default Home;